export default {
	data() {
		return {
			newsid: 0,
			info: {}
		}
	},
	onLoad(e) {
		console.log(e.newsid)
		this.newsid = e.newsid
		
		this.getData()
	},
	methods: {
		getData: function() {
			uni.showLoading({
				title: "加载中..."
			})
			this.$http.request({
			    url: '/music/getDetail',
			    method: 'post',
			    header: {},
			    params: {
					id: this.newsid
			    }
			}).then(res => {
				uni.hideLoading()
				console.log(res.data.success)
				console.log(res.data.data)
				this.info = res.data.data
			}).catch(err => {
				uni.hideLoading()
				console.log(err)
			})
		}
	}
}
